@use 'base/variables' as v;

.alerta {

    margin: 2rem 0;
    padding: 1rem 2rem;
    text-transform: uppercase;
    font-size: 1.4rem;


    &__error {
        background-color: lighten(v.$rojo, 60%);
        border-left: .5rem solid v.$rojo;
        color: v.$rojo;
    }

    &__exito {
        background-color: lighten(v.$verde, 60%);
        border-left: .5rem solid v.$verde;
        color: v.$verde;
    }
}